.settings__frame {
    font-family: $FONTROBOTO;
    background-color: white;
    padding: $PADDING * 2;
    border-radius: $RADIUS;
    width: 500px; }

.settings__title {
    padding-bottom: $PADDING * 3;
    border-bottom: $DIVIDER;
    h3 {
        font-family: inherit;
        font-size: 20px;
        font-weight: bold;
        color: $SECONDARYCOLOR;
        padding: $PADDING;
        line-height: 24px; } }

.settings__practice {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: nowrap;
    color: $BLACKTEXTSECONDARY;
    padding: $PADDING;
    border-bottom: $DIVIDER; }

.settings__practice__set {
    font-size: 16px;
    background-color: #f5f5f5;
    color: rgba(0,0,0,.54);
    padding: 0 10px;
    width: 220px;
    border: none;
    border-radius: 3px;
    outline: none;
    height: 48px; }

.settings__table {
    padding: $PADDING;
    input {
        box-sizing: border-box;
        font-size: 15px;
        font-weight: bold;
        background-color: #F5F5F5;
        color: $BLACKTEXTSECONDARY;
        padding: 0 $PADDING * 2;
        margin: 14px 0;
        border: none;
        border-radius: $RADIUS;
        outline: none;
        width: 100%;
        height: 48px; }
    input:focus {
        background-color: #E0E0E0; } }

.settings__button__block {
    font-family: $FONTROBOTOCONDENSED;
    padding: $PADDING; }

.settings__button__set, .settings__button__cancel {
    font-family: inherit;
    font-size: 16px;
    font-weight: bold;
    background-color: #FFF;
    color: $SECONDARYCOLOR;
    cursor: pointer;
    padding: 0 $PADDING;
    margin: 0 0 0 8px;
    border: none;
    border-radius: $RADIUS;
    float: right;
    letter-spacing: 0.3px;
    line-height: 36px;
    outline: none;
    text-transform: uppercase; }

.settings__button__set:hover, .settings__button__cancel:hover {
    background-color: #F5F5F5; }

.settings__button__set:active, .settings__button__cancel:active {
    background-color: #E0E0E0; }
