%page {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 100%;
    height: 100%; }

.intro {
    @extend %page;
    overflow: hidden;
    z-index: 0; }

.main {
    @extend %page;
    display: none;
    z-index: 1; }

.settings {
    @extend %page;
    display: none;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 2; }

.table {
    @extend %page;
    display: none;
    background-color: $GREY;
    min-width: 29.7cm;
    min-height: 21cm;
    z-index: 3; }
